@import url(https://fonts.googleapis.com/css2?family=Quintessential&family=Source+Serif+4:wght@300&display=swap);
body {
  margin: 0;
  font-family: "Source Serif 4", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #faf9f7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.input h2 {
  color: #2d4263;
  font-weight: bolder;
  font-family: "Quintessential", cursive;
}

.input form input {
  border: none;
  border-bottom: 2px solid rgb(11, 91, 182);
  background-color: transparent;
  padding: 3px;
  outline: none;
  font-size: 1.1rem;
  font-weight: 500;
}

.input form button {
  margin-left: 10px;
  border-radius: 2px;
  padding: 5px;
  border: none;
  background-color: rgb(11, 91, 182);
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.input form button:hover{
    background-color: rgb(34, 111, 199);
}

.data {
  margin: 15px;
}

.row {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

img {
  height: 250px;
  width: 250px;
  border-radius: 5px;
  border: 2px solid rgb(26, 25, 25);
}

.nutrients-list {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.nutrients {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 5px;
  font-weight: 500;
  padding: 5px 7px;
}

.recipe {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 300px;
  height: 350px;
  margin: 50px 20px;
}

.recipe img {
  height: 250px;
  width: 250px;
  border-radius: 5px;
  border: 2px solid rgb(26, 25, 25);
}

.recipe button {
  background-color: rgb(11, 91, 182);
  width: 150px;
  border: none;
  padding: 6px 7px;
  color: white;
  font-size: 1rem;
  font-weight: 700;
  border-radius: 2px;
  margin: 15px 10px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.recipe button:hover {
  background-color: rgb(36, 110, 196);
}

